.pelicula-content-grid{
  margin: auto;
  display: grid;
  /* grid-template-columns: 1fr 1fr; */
  grid-template-columns: repeat(auto-fit, minmax(165px, 1fr));
  justify-items: center;
}

.pelicula-seccion{
  height: min-max(340px);
  width: min-max(165px);
  padding: .5rem;
  margin: .5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(23, 23, 83);
  border-radius: 10px;
  border: 1px solid rgba(0, 0, 0, 0.473);
  box-shadow: 8px 10px 5px 0px rgba(0,0,0,0.75);
  
}

.titulo-seccion-content{
  width: 100%;
  min-height: 84px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.img-poster-content-seccion{
  width: 100%;
}

.img-poster-seccion{
  width: 100%;
  min-height: 198.733px;
  border-radius: 10px;
}

/********** botones paginacion ************/

.pagina{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: .5rem;
  height: 1.8rem;
}

.siguienteBtn, 
.anteriorBtn {
  width: 1.5rem;
  font-size: 1rem;
  cursor: pointer;
  user-select: none;
}

.numero-pagina{
  margin: 0 .5rem 0 1rem;
  font-size: 1.3rem;
  padding: 0 .5rem;
  background-color: rgb(62, 9, 111);
  border: none;
  border-radius: 10px;
  color: inherit;
}

.total-paginas{
  margin: 0 1rem 0 .5rem;
}
