

::-webkit-scrollbar {
  display: none;
}

hr{
  margin: 1.5rem 0;
  color: rgb(70, 8, 70);
  box-shadow: 8px 3px 5px 0px rgba(88, 11, 70, 0.856);

}

a{
  text-decoration: none;
  color: inherit;
}

html, body, #root {
  min-height: 100vh;
}

/********* Estilo para carga de peliculas ***********/

.seccion-peliculas{
  display: flex;
  flex-direction: column;
}

.pelicula-content{
  display: flex;
  overflow-x: scroll;
  overscroll-behavior-x: contain;
  scroll-snap-type: x proximity;
  background-color: rgb(35, 3, 75);
  box-shadow: 8px 10px 5px 0px rgba(0,0,0,0.75);
  border: 1px solid black;
}

.pelicula{
  min-height: 350px;
  min-width: 165px;
  padding: .5rem .5rem 0 .5rem;
  margin: .5rem .25rem 1rem .5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(23, 23, 83);
  border-radius: 10px;
  border: 1px solid rgba(0, 0, 0, 0.473);
  box-shadow: 8px 10px 5px 0px rgba(0,0,0,0.75);
}

.img-poster-content{
  height: 221.5px;
  /* max-height: 257.5px; */
  overflow: hidden;
  border-radius: 10px;
  background-size: cover;
}

.img-poster{
  width: 100%;
  height: 100%;
  border-radius: 10px;
}

.titulo-content{
  width: 100%;
  min-height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.titulo-pelicula{
  text-align: center;
  font-size: 1.15rem;
}

.tipo-peliculas{
  font-size: 2rem;
  text-transform: capitalize;
  margin: .3rem 0 .5rem .8rem;
}

.calificacion{
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: .3rem;
}

.estrella-img{
  width: 1.3rem;
  margin-top: -.1rem;
  margin-right: .2rem;
}

.nombre-pagina{
  color: rgb(149, 151, 152);
  font-size: 2rem;
  width: 100%;
  text-align: center;
  padding: 1rem 0;
  background-color: rgb(17, 1, 31);
}

/************* Boton ver mas ************/

.ver-mas-btn{
  font-size: 1.5rem;
  font-weight: 500;
  padding: .5rem;
  text-decoration: none;
  margin: 1.2rem auto 0 auto;
  background-color: rgb(42, 8, 77);
  border-radius: 10px;
  box-shadow: 8px 10px 5px 0px rgba(0,0,0,0.75);

}

.not-found{
  min-height: calc(100vh - 226px);
}

@media only screen and (min-width: 1024px){
  .nombre-pagina{
    font-size: 2.5rem;
  }

  .botones-content{
    display: none;
  }

  .tipo-peliculas{
    width: 90vw;
    margin: 0 auto 1rem;
  }

  .pelicula-content{
    width: 90vw;
    margin: 0 auto;
  }

  .footer-main{
    width: 30vw;
    margin: 0 auto;
  }

  .nombre-codificador{
    position: relative;
  }

  .pelicula-content-grid{
    width: 82vw;
  }

  .ver-trailer{
    width: 90vw;

    margin: 0 auto;
    background-color: none;
  }


  .banner-pelicula{
    display: none;
  }

  .detalles-pelicula-content{
    width: 80vw;
    margin: 0 auto;
  }

  .pelicula-poster{
    width: 250px;
  }

  .detalles-principales{
    width: 90vw;
    justify-content: center;
  }
  
  .detalles-principales h3{
    font-size: 1.3rem;
  }
  
  .pelicula-sinopsis{
    width: 75%;
    font-size: 1.3rem;

  }

  .banner-content h2{
    font-size: 2.5rem;
    height: 150px;
    -webkit-text-stroke: 2px rgb(152, 159, 160);
  }

  .trailer-pelicula{
    display: flex;
    justify-content: center;
    align-items: center;
  }

}