.pelicula-poster{
  max-width: 45vw;
  max-height: 68vw;
  margin-right: .5rem;
}

.banner-content{
  width: 100%;
  min-height: 200px;
  position: relative;
  display: inline-block;
  text-align: center;
}

.banner-pelicula{
  width: 100%;
}

.pelicula-titulo{
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-text-stroke: 1px rgb(152, 159, 160);
  color: rgb(0, 0, 0);
  font-weight: 900;
  background-color: rgba(103, 97, 106, 0.167);
  padding: 0 2rem;
  font-size: 1.6rem;
}

.detalles-pelicula-content{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.detalles-principales{
  width: 99vw;
  padding: .5rem;
  display: flex;
}

.detalles-principales h3 {
  margin-bottom: .3rem;
}

.detalles-principales-texto{
  display: flex;
  flex-direction: column;
}

.pelicula-sinopsis{
  margin: .6rem;
  text-align: justify;
}

.trailer-pelicula{
  width: 100%;
  position: relative;
}

.trailer-texto-error{
  text-align: center;
}

.play-icon{
  width: 4rem;
}

.cerrar-icon{
  width: 1.5rem;
}

.boton-ver-trailer-content{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.boton-ocultar-trailer-content{
  display: flex;
  align-items: center;
  position: absolute;
  top: 380px;
  left: 10px;
}
.boton-ocultar-trailer{
  background-color: grey;
  border: none;
  padding: .2rem;
  margin-left: .3rem;
  border-radius: 10px;
}

.boton-ver-trailer{
  background-color: rgba(0, 0, 0, 0.537);
  color: white;
  border: none;
  padding: .3rem;
  border-radius: 10px;
  font-size: 1rem;
}

.ocultar{
  display: none;
}

.mostrar{
  display: block;
}

h3{
  color: rgb(185, 185, 176);
  font-size: 1.1rem;
}

small{
  color: rgb(106, 91, 91);
}